* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: #333;
  width: 100vw;
  font-family: "PT Sans Narrow", sans-serif;
  font-size: 1.2em;
}

a,
a:focus {
  color: #000;
}

img {
  max-width: 100%;
  width: 100vw;
}

hr {
  margin: 0;
}

.front-div {
  position: relative;
  z-index: 2;
  background-color: #fefefe;
}

.gray {
  background-color: #eee;
}

.white {
  background-color: #fefefe;
}

#particles {
  display: none;
  z-index: 1;
}

/* Button */

#button button {
  color: #fefefe;
  background-color: #4eacf3;
  padding: 10px 60px;
  border-radius: 100px;
  -webkit-box-shadow: 0px 1px 5px 1px lightgrey;
          box-shadow: 0px 1px 5px 1px lightgrey;
  -webkit-transition: all 0.1s ease-in-out;
  -o-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}

#button button:hover,
#button button:active {
  background-color: #3aa3f2 !important;
}

/* Divider  */

.divider {
  width: 100vw;
  height: 150px;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

/* Header */

#headshot-pic {
  border-radius: 100%;
  width: 280px;
  height: 250px;
  border: 2px solid rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0px 4px 6px 2px lightgrey;
          box-shadow: 0px 4px 6px 2px lightgrey;
  margin: 20px;
}

.icons > a svg {
  font-size: 1.6em;
  margin: 0 7px;
  -webkit-box-shadow: 0px 4px 6px 2px lightgrey;
          box-shadow: 0px 4px 6px 2px lightgrey;
}

/* About & Projects */

#about {
  padding-bottom: 20px;
}

#projects div,
#about div {
  padding: 20px 10px;
}

#projects div {
  text-align: center;
}

#about p {
  margin: 0 20px;
}

#projects h2,
#about h2 {
  text-align: left;
  padding: 20px 20px;
}

#projects h3 {
  margin-bottom: 20px;
  display: inline-block;
  font-weight: bold;
  /* border-bottom: 3px solid #333; */
}

.mockup-img {
  margin: 10px 0;
}

.resize {
  max-width: 60vw;
}

.tech-stack img {
  width: 100px;
  margin: 0px 5px;
}

/* Contact */

#contact {
  display: -ms-flexbox;
  display: flex;
  height: 200px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  body {
    font-size: 1.3em;
  }
  .inner {
    width: 75%;
  }
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  body {
    font-size: 1.5em;
  }

  .inner {
    width: 60vw;
  }

  .mockup-img {
    max-width: 50vw;
  }

  .resize {
    max-width: 40vw;
  }
}

/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  h2 {
    font-size: 1.7em;
  }

  h3 {
    font-size: 1.6em;
  }

  h5 {
    font-size: 1.1em;
  }

  .inner {
    width: 90vw;
  }

  .outer,
  .inner {
    position: relative;
    z-index: 1;
  }

  #particles {
    display: block;
  }

  /* Header */

  #header {
    width: 600px;
  }

  /* About & Projects */

  .mockup-img {
    max-width: 40vw;
  }

  .resize {
    max-width: 30vw;
  }

  .flex {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }

  #projects h2 {
    text-align: center;
  }

  #projects p {
    padding: 0 100px;
  }

  .flex-left {
    -ms-flex-order: 1;
        order: 1;
  }

  .flex-right {
    -ms-flex-order: 2;
        order: 2;
  }
}



